export default "import React from 'react';\nimport MarkerGauge from '@splunk/dashboard-visualizations/MarkerGauge';\n\nexport default () => (\n    <MarkerGauge\n        options={{\n            ranges: [\n                {\n                    from: -10,\n                    to: 0,\n                    value: '#ff7152',\n                },\n                {\n                    from: 0,\n                    to: 20,\n                    value: '#ff9800',\n                },\n                {\n                    from: 20,\n                    to: 30,\n                    value: '#cddc39',\n                },\n                {\n                    from: 30,\n                    to: 40,\n                    value: '#00bcd4',\n                },\n            ],\n        }}\n        dataSources={{\n            primary: {\n                requestParams: { offset: 0, count: 20 },\n                data: {\n                    fields: [{ name: 'count' }],\n                    columns: [['35']],\n                },\n                meta: { totalCount: 20 },\n            },\n        }}\n    />\n);\n";